@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  text-decoration: none;
  color: inherit;
}

.slider-wrapper {
  width: 100%;
  /* overflow: hidden; */
}
img {
  width: 100%;
}

.home {
  width: 100%;
  margin-top: 52px;
}
.cards-wrapper {
  width: 92%;
  margin: 6% auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  grid-row-gap: 30px;
  row-gap: 30px;
}

.imgContainer {
  width: 40%;
}
.productDetailsContainer {
  width: 55%;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}
.name {
  font-size: 40px !important;
  font-weight: 400 !important;
  font-family: 'Montserrat' !important;
  letter-spacing: -2px !important;
}
.brandName {
  margin-top: 20px !important;
  font-size: 20px !important;
  font-family: 'Montserrat' !important;
  letter-spacing: 1px !important;
  color: rgb(27, 25, 25) !important;
}
.productDetailsHeading {
  margin-top: 20px !important;
  font-size: 20px !important;
  font-family: 'Montserrat' !important;
  font-weight: 400 !important;
  letter-spacing: 1px !important;
  color: rgb(27, 25, 25) !important;
}
.price {
  color: #009688 !important;
  font-weight: 500 !important;
}
.desc {
  margin-top: 20px !important;
  font-size: 16px !important;
  color: #86939e !important;
  font-family: 'Montserrat' !important;
}
.previewImages {
  margin: 5px;
  width: 15%;
  height: 30%;
  cursor: pointer;
  border-radius: 6px;
  border: 1px solid transparent;
}
.active {
  border: 2px solid #009688;
}
.addToCartBtn {
  background-color: #009688 !important;
  color: #fff !important;
  margin-top: 72px !important;
  transition: all 0.3 linear;
}

.addToCartBtn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.topbar {
  background-color: #fff !important;
  font-size: 14px;
  color: rgb(27, 25, 25) !important;
  padding: 0 32px !important;
}
.toolbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.MuiBadge-badge {
  background-color: #009688 !important;
  color: #fff;
}
.MuiBadge-anchorOriginTopRightRectangle {
  top: -4px !important;
  right: -4px !important;
}
.brand-title {
  text-transform: uppercase;
  font-size: 30px !important;
}
.brand {
  font-weight: 600;
  color: #009688;
}
.MuiInputBase-input {
  border: 1px solid #aaa !important;
  border-radius: 20px;
}

.container {
  width: 90%;
  margin: 80px auto;
}
.sectionHeading {
  font-weight: 400 !important;
  font-size: 40px !important;
  letter-spacing: -2px !important;
  font-family: 'Montserrat' !important;
  margin-bottom: 3% !important;
}
.totalItemsHeading {
  font-size: 20px !important;
  letter-spacing: 1px !important;
  font-family: 'Montserrat' !important;
}
.cardContainer {
  margin-top: 4% !important;
}
.leftSection {
  width: 52%;
}
.rightSection {
  width: 40%;
  margin-right: 32px !important;
  padding: 15px !important;
}
.productCard {
  padding: 16px;
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 0 10px #aaa !important;
  margin-bottom: 20px;
}
.productCard > img {
  width: 80px;
  height: 100px;
  margin-right: 20px;
}
.productCard h4 {
  font-size: 20px;
  font-weight: 500;
  margin: 0%;
  margin-bottom: 12px;
}
.productCard h5 {
  font-size: 16px;
  font-weight: 400;
  margin: 0%;
  margin-bottom: 12px;
  color: rgb(27, 25, 25);
  letter-spacing: 1px;
}
.productCard h6 {
  font-size: 18px;
  font-weight: 400;
  margin: 0%;
  margin-right: 12px;
  color: rgb(27, 25, 25);
}
.checkOutDetails div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.checkOutDetails div button {
  color: rgb(27, 25, 25) !important;
}
.rightSection .card {
  padding: 15px !important;
}
.totalAmountHeading {
  letter-spacing: 2px !important;
  font-family: 'Montserrat' !important;
  font-size: 24px !important;
  font-weight: 400 !important;
}
.totalAmountPara {
  margin-top: 30px !important;
  font-size: 16px !important;
  letter-spacing: 2px !important;
  font-family: 'Montserrat' !important;
  font-weight: 400 !important;
}
.totalAmount {
  font-weight: bold;
  font-size: 20px;
}

.placeOrderBtn {
  background-color: #009688 !important;
  color: #fff !important;
  margin-top: 72px !important;
  transition: all 0.3 linear;
}

.placeOrderBtn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.MuiButtonBase-root.Mui-disabled {
  cursor: not-allowed !important;
  pointer-events: initial;
}
.MuiButtonBase-root.Mui-disabled {
  -webkit-transform: scale(1);
          transform: scale(1);
}

footer {
  width: 100%;
  background-color: #000;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
  padding: 2% 8%;
}
footer > div {
  padding-left: 60px;
  padding-right: 60px;
}

footer > div:first-of-type {
  padding-left: 0;
}

.footerHeading {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  color: white;
  opacity: 0.9;
  letter-spacing: 1px;
}

footer a {
  text-decoration: none;
  color: white;
  display: block;
  margin-top: 5px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;
  opacity: 0.8;
}
footer a:hover {
  color: #009866;
}

.thankyouSection {
  padding: 6% 8% 4%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.thankyouSection img {
  width: 400px;
}
.thankyouSection h3 {
  letter-spacing: 0.8px;
  font-size: 24px;
  margin: 0%;
  font-weight: 600;
}
.thankyouSection p {
  margin: 0;
  margin-top: 12px;
  letter-spacing: 0.5px;
}

